import React from 'react';
import {
  View,
  Text,
  StyleSheet,
  Switch,
  Button,
} from 'react-native';
import { useAuth } from '../context/AuthContext';
import { useSettings } from '../context/SettingsContext';
import { useTheme } from '../context/ThemeContext';

const SettingsScreen = ({ navigation }) => {
  const { user, logout } = useAuth();
  const {
    soundOn,
    darkMode,
    showHints,
    fullScreen,
    updateSound,
    updateDarkMode,
    updateHints,
    updateFullScreen
  } = useSettings();
  const theme = useTheme();

  const handleLogout = async () => {
    await logout();
    navigation.replace('Login');
  };

  return (
    <View style={[styles.container, { backgroundColor: theme.background }]}>
      <Text style={[styles.title, { color: theme.text }]}>Settings</Text>

      <View style={styles.settingRow}>
        <Text style={[styles.label, { color: theme.text }]}>Sound</Text>
        <Switch
          value={soundOn}
          onValueChange={updateSound}
          thumbColor={soundOn ? theme.highlight : '#888'}
        />
      </View>

      <View style={styles.settingRow}>
        <Text style={[styles.label, { color: theme.text }]}>Dark Mode</Text>
        <Switch
          value={darkMode}
          onValueChange={updateDarkMode}
          thumbColor={darkMode ? theme.highlight : '#888'}
        />
      </View>

      <View style={styles.settingRow}>
        <Text style={[styles.label, { color: theme.text }]}>Hints</Text>
        <Switch
          value={showHints}
          onValueChange={updateHints}
          trackColor={{ false: '#767577', true: '#81b0ff' }}
          thumbColor={showHints ? '#2196F3' : '#f4f3f4'}
        />
      </View>

      <View style={styles.settingRow}>
        <Text style={[styles.label, { color: theme.text }]}>Full Screen</Text>
        <Switch
          value={fullScreen}
          onValueChange={updateFullScreen}
          trackColor={{ false: '#767577', true: '#81b0ff' }}
          thumbColor={fullScreen ? '#2196F3' : '#f4f3f4'}
        />
      </View>

      <Text style={[styles.description, { color: theme.border }]}>
        When enabled, hints will show the first and last letter of the longest possible word.
      </Text>

      <Text style={[styles.description, { color: theme.border }]}>
        Full screen mode maximizes the game area on mobile browsers.
      </Text>

      <View style={styles.userInfo}>
        <Text style={[styles.userLabel, { color: theme.border }]}>Username:</Text>
        <Text style={[styles.userValue, { color: theme.text }]}>
          {user?.username || 'Guest'}
        </Text>
        <Text style={[styles.userLabel, { color: theme.border }]}>Email:</Text>
        <Text style={[styles.userValue, { color: theme.text }]}>
          {user?.email || 'Not set'}
        </Text>
      </View>

      <View style={styles.buttonContainer}>
        <Button title="Logout" onPress={handleLogout} />
        <Button title="Back to Menu" onPress={() => navigation.goBack()} />
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    padding: 20,
  },
  title: {
    fontSize: 24,
    fontWeight: 'bold',
    marginBottom: 20,
    textAlign: 'center',
  },
  settingRow: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    marginBottom: 15,
    paddingVertical: 10,
    borderBottomWidth: 1,
    borderBottomColor: 'rgba(0,0,0,0.1)',
  },
  label: {
    fontSize: 16,
  },
  description: {
    fontSize: 14,
    marginBottom: 20,
    fontStyle: 'italic',
  },
  userInfo: {
    marginTop: 20,
    marginBottom: 30,
    padding: 15,
    borderRadius: 8,
    backgroundColor: 'rgba(0,0,0,0.05)',
  },
  userLabel: {
    fontSize: 14,
    marginBottom: 5,
  },
  userValue: {
    fontSize: 16,
    fontWeight: 'bold',
    marginBottom: 15,
  },
  buttonContainer: {
    marginTop: 20,
    gap: 10,
  },
});

export default SettingsScreen;
