import React, { createContext, useEffect, useState, useContext } from 'react';
import AsyncStorage from '@react-native-async-storage/async-storage';

const SettingsContext = createContext();

export const useSettings = () => {
  const context = useContext(SettingsContext);
  if (!context) {
    throw new Error('useSettings must be used within a SettingsProvider');
  }
  return context;
};

export const SettingsProvider = ({ children }) => {
  const [soundOn, setSoundOn] = useState(true);
  const [darkMode, setDarkMode] = useState(false);
  const [showHints, setShowHints] = useState(true);
  const [fullScreen, setFullScreen] = useState(false);

  useEffect(() => {
    loadSettings();
  }, []);

  const loadSettings = async () => {
    try {
      const settings = await AsyncStorage.getItem('userSettings');
      if (settings) {
        const parsedSettings = JSON.parse(settings);
        setSoundOn(parsedSettings.soundOn ?? true);
        setDarkMode(parsedSettings.darkMode ?? false);
        setShowHints(parsedSettings.showHints ?? true);
        setFullScreen(parsedSettings.fullScreen ?? false);
      }
    } catch (error) {
      console.error('Error loading settings:', error);
    }
  };

  const saveSettings = async (newSettings) => {
    try {
      await AsyncStorage.setItem('userSettings', JSON.stringify(newSettings));
    } catch (error) {
      console.error('Error saving settings:', error);
    }
  };

  const updateSound = (value) => {
    setSoundOn(value);
    saveSettings({ soundOn: value, darkMode, showHints, fullScreen });
  };

  const updateDarkMode = (value) => {
    setDarkMode(value);
    saveSettings({ soundOn, darkMode: value, showHints, fullScreen });
  };

  const updateHints = (value) => {
    setShowHints(value);
    saveSettings({ soundOn, darkMode, showHints: value, fullScreen });
  };

  const updateFullScreen = (value) => {
    setFullScreen(value);
    saveSettings({ soundOn, darkMode, showHints, fullScreen: value });
    
    // Toggle full screen mode
    if (value) {
      document.documentElement.requestFullscreen().catch(err => {
        console.error(`Error attempting to enable fullscreen: ${err.message}`);
      });
    } else {
      if (document.exitFullscreen) {
        document.exitFullscreen();
      }
    }
  };

  return (
    <SettingsContext.Provider
      value={{
        soundOn,
        darkMode,
        showHints,
        fullScreen,
        updateSound,
        updateDarkMode,
        updateHints,
        updateFullScreen
      }}
    >
      {children}
    </SettingsContext.Provider>
  );
};
