import React from 'react';
import { View, Button, StyleSheet } from 'react-native';

const ActionButtons = ({ onBackspace, onTradeIn, onShuffle, currentWord }) => {
  return (
    <View style={styles.row}>
      <Button title="BACKSPACE" onPress={onBackspace} />
      <Button 
        title={currentWord && currentWord.length > 0 ? "TRADE IN" : "QUIT"} 
        onPress={onTradeIn} 
      />
      <Button title="SHUFFLE" onPress={onShuffle} />
    </View>
  );
};

const styles = StyleSheet.create({
  row: {
    flexDirection: 'row',
    justifyContent: 'space-around',
    marginVertical: 10,
  },
});

export default ActionButtons;
