import { LETTER_DATA } from '../constants/letterData';
import { VOWELS } from './vowelUtils';

let cachedWordLists = {};

//  Load word list from public folder using fetch (works on web & native)
export const loadWordList = async (language) => {
  if (cachedWordLists[language]) {
    console.log(` Using cached wordlist for ${language} (${cachedWordLists[language].size} words)`);
    return cachedWordLists[language];
  }

  const key = language.toLowerCase();

  try {
    // For React Native, we need to use require
    const wordListPath = require(`../public/wordlists/${key}.txt`);
    
    // Read the file content
    const response = await fetch(wordListPath);
    if (!response.ok) {
      throw new Error(`Failed to load wordlist: ${response.status}`);
    }
    
    const fileContent = await response.text();
    const words = fileContent.split('\n').map((w) => w.trim().toUpperCase());
    const wordSet = new Set(words);
    
    console.log(` Loaded ${wordSet.size} words for ${language}`);
    console.log(` Sample words:`, Array.from(wordSet).slice(0, 5));
    
    cachedWordLists[language] = wordSet;
    return wordSet;
  } catch (error) {
    console.error(` Failed to load wordlist for ${language}:`, error);
    return new Set();
  }
};

//  Check if a letter is a vowel
const isVowel = (char) => ['A', 'E', 'I', 'O', 'U'].includes(char);

//  Generate a random letter grid with 2+ vowels and 2+ consonants
export const getRandomLetters = (count = 9, language = 'English', requireBalanced = true, remainingLetters = []) => {
  const letterData = LETTER_DATA[language] || LETTER_DATA.English;
  const vowelSet = new Set(VOWELS[language] || VOWELS.English);

  // Count occurrences of each letter in remaining letters
  const letterCounts = {};
  remainingLetters.forEach(letter => {
    letterCounts[letter] = (letterCounts[letter] || 0) + 1;
  });

  // Create a weighted selection function based on frequencies
  const getWeightedRandomLetter = (isVowel = null) => {
    // Filter letters based on vowel/consonant requirement and max count
    const availableLetters = Object.entries(letterData).filter(([char]) => {
      // Check if letter is already at max count (2)
      if (letterCounts[char] >= 2) return false;
      
      // Check vowel/consonant requirement if specified
      if (isVowel === null) return true;
      return isVowel ? vowelSet.has(char) : !vowelSet.has(char);
    });

    // If no letters are available, return null
    if (availableLetters.length === 0) return null;

    // Calculate total weight of available letters
    const totalWeight = availableLetters.reduce((sum, [_, { frequency }]) => sum + frequency, 0);

    // Generate random value between 0 and total weight
    let random = Math.random() * totalWeight;

    // Select letter based on weight
    for (const [char, { frequency }] of availableLetters) {
      random -= frequency;
      if (random <= 0) {
        // Update letter count
        letterCounts[char] = (letterCounts[char] || 0) + 1;
        return char;
      }
    }

    // Fallback to first available letter (should never happen)
    const fallbackChar = availableLetters[0][0];
    letterCounts[fallbackChar] = (letterCounts[fallbackChar] || 0) + 1;
    return fallbackChar;
  };

  // Count vowels and consonants in remaining letters
  const remainingVowels = remainingLetters.filter(l => vowelSet.has(l)).length;
  const remainingConsonants = remainingLetters.filter(l => !vowelSet.has(l)).length;
  
  // Calculate how many vowels and consonants we need to add
  const totalVowelsNeeded = Math.max(2, remainingVowels + 2);
  const totalConsonantsNeeded = Math.max(2, remainingConsonants + 2);
  
  // Calculate how many vowels and consonants we need in the new letters
  const newVowelsNeeded = Math.max(0, totalVowelsNeeded - remainingVowels);
  const newConsonantsNeeded = Math.max(0, totalConsonantsNeeded - remainingConsonants);

  const generate = () => {
    const letters = [];
    
    // First, add required vowels if needed
    while (letters.length < newVowelsNeeded && letters.length < count) {
      const letter = getWeightedRandomLetter(true);
      if (letter === null) break; // No more available vowels
      letters.push(letter);
    }
    
    // Then, add required consonants if needed
    while (letters.length < (newVowelsNeeded + newConsonantsNeeded) && letters.length < count) {
      const letter = getWeightedRandomLetter(false);
      if (letter === null) break; // No more available consonants
      letters.push(letter);
    }
    
    // Fill the rest with random letters
    while (letters.length < count) {
      const letter = getWeightedRandomLetter();
      if (letter === null) break; // No more available letters
      letters.push(letter);
    }
    
    return letters;
  };

  const isBalanced = (letters) => {
    const vowels = letters.filter((l) => vowelSet.has(l));
    const consonants = letters.filter((l) => !vowelSet.has(l));
    
    // Check if the total (new + remaining) has enough vowels and consonants
    const totalVowels = vowels.length + remainingVowels;
    const totalConsonants = consonants.length + remainingConsonants;
    
    return totalVowels >= 2 && totalConsonants >= 2;
  };

  // If balance isn't required (e.g. tradeIn < 4), skip checking
  if (!requireBalanced || count < 4) {
    return generate();
  }

  let attempt = 0;
  while (attempt < 10) {
    const result = generate();
    if (isBalanced(result)) return result;
    attempt++;
  }

  // Fallback after 10 tries - ensure at least some vowels and consonants
  const fallbackResult = generate();
  console.log(" Using fallback letter generation after 10 attempts");
  return fallbackResult;
};


//  Check if a word is valid
export const isValidWord = (word, wordListSet) => {
  return wordListSet.has(word.toUpperCase());
};
