import React, { useContext, useEffect, useState } from 'react';
import { View, Text, StyleSheet } from 'react-native';
import { GameContext } from '../context/GameContext';
import { useSettings } from '../context/SettingsContext';
import { LanguageContext } from '../context/LanguageContext';
import { useAuth } from '../context/AuthContext';
import { useTheme } from '../context/ThemeContext';
import { loadWordList } from '../utils/wordUtils';
import TimerDisplay from './TimerDisplay';

const HeaderBar = () => {
  const { 
    score, 
    gridLetters, 
    gameTime, 
    timerRunning, 
    timerFrozen,
    freezeTimeLeft,
    alertMessage 
  } = useContext(GameContext);
  const { showHints } = useSettings();
  const { language } = useContext(LanguageContext);
  const { user } = useAuth();
  const theme = useTheme();

  const [wordList, setWordList] = useState(new Set());
  const [hint, setHint] = useState('');
  const username = user?.username || 'Guest';

  // Debug timer state in HeaderBar
  useEffect(() => {
    console.log(' HeaderBar timer state:', { 
      gameTime, 
      timerRunning, 
      timerFrozen,
      freezeTimeLeft
    });
  }, [gameTime, timerRunning, timerFrozen, freezeTimeLeft]);

  useEffect(() => {
    const fetchList = async () => {
      const words = await loadWordList(language);
      setWordList(words);
    };
    fetchList();
  }, [language]);

  // Generate hint when grid letters change
  useEffect(() => {
    if (!showHints || gridLetters.length === 0 || wordList.size === 0) {
      setHint('');
      return;
    }

    const letters = gridLetters.join('');
    let longest = '';

    wordList.forEach(word => {
      const upperWord = word.toUpperCase();
      if (upperWord.length >= 3 && isWordInGrid(upperWord, letters)) {
        if (upperWord.length > longest.length) {
          longest = upperWord;
        }
      }
    });

    if (longest.length >= 3) {
      const hintString = `${longest[0]}${'-'.repeat(longest.length - 2)}${longest[longest.length - 1]}`;
      setHint(hintString);
    } else {
      setHint('');
    }
  }, [showHints, gridLetters, wordList]);

  const isWordInGrid = (word, letters) => {
    const tempLetters = [...letters];
    for (let char of word) {
      const index = tempLetters.indexOf(char);
      if (index === -1) return false;
      tempLetters.splice(index, 1);
    }
    return true;
  };

  return (
    <View style={[styles.container, { backgroundColor: theme.card }]}>
      <View style={styles.scoreContainer}>
        <Text style={[styles.label, { color: theme.text }]}>Score:</Text>
        <Text style={[styles.value, { color: theme.text }]}>{score}</Text>
      </View>
      
      <View style={styles.alertContainer}>
        {alertMessage ? (
          <Text style={[styles.alertText, { color: theme.highlight }]}>{alertMessage}</Text>
        ) : hint ? (
          <Text style={[styles.hintText, { color: theme.text }]}>Hint: {hint}</Text>
        ) : null}
      </View>
      
      <View style={styles.timerContainer}>
        <TimerDisplay />
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    padding: 10,
    borderRadius: 8,
    marginBottom: 10,
  },
  scoreContainer: {
    flex: 1,
    alignItems: 'flex-start',
  },
  alertContainer: {
    flex: 2,
    alignItems: 'center',
    justifyContent: 'center',
  },
  timerContainer: {
    flex: 1,
    alignItems: 'flex-end',
  },
  label: {
    fontSize: 12,
    marginBottom: 2,
  },
  value: {
    fontSize: 16,
    fontWeight: 'bold',
  },
  alertText: {
    fontSize: 14,
    fontWeight: 'bold',
    textAlign: 'center',
  },
  hintText: {
    fontSize: 14,
    fontStyle: 'italic',
    textAlign: 'center',
  },
});

export default HeaderBar;
